import React from 'react'
import cx from 'classnames'
import { scoreToFixed } from 'Shared/helpers/ats'
import './ScoreTag.scss'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { ICONS } from 'Shared/assets/images/imageCommon.js'

function ScoreTag (props) {
  if (!props.scoreData) return (<div className={`score-tag score-`}>{props.title}</div>)
  const { title, handleOnClick, scoreData: { score, atsStatus } } = props

  const ATS_STATUS = {
    'failed': 'Score not available',
    'pass': 'pass',
    '-1': 'loading',
    '-2': 'Score not available'
  }

  if (score === '-1') return (<div className={`score-tag`}> <span className={ATS_STATUS[score.toString()]}></span> {ATS_STATUS[score.toString()]}</div>)
  if (ATS_STATUS[score.toString()] !== undefined) return (<div className={`score-tag`} data-score={scoreToFixed(score)}>{ATS_STATUS[score.toString()]}</div>)

  const scoreClass = cx({
    'low': score <= 35,
    'medium': score >= 35 && score < 72,
    'high': score >= 72 && score <= 100
  })

  return (<React.Fragment>
    <div className={`score-tag ${scoreClass}`} onClick={() => handleOnClick()}>
      Score {scoreToFixed(score)} / 100
      <Tooltip className='tooltip text-left' style={{ textAlign: 'left' }}
               trigger='mouseenter' content={`Olivia ATS score indicates applicant's alignment with the job description.
Higher scores signal better fit.
Use this to assess candidate suitability.`}>
        <img className='icon-info' src={ICONS.info} />
      </Tooltip>
    </div>
  </React.Fragment>)
}

export default ScoreTag



