import React, { useState } from 'react'
import cx from 'classnames'
import Table from 'Shared/components/Table'
import { camelCaseToWords, getNumberOnly } from 'Shared/helpers/string'
import { scoreToFixed } from 'Shared/helpers/ats'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import { getSessionData } from 'Shared/helpers/session'
import './ScoreDrawer.scss'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { ICONS } from 'Shared/assets/images/imageCommon.js'

function ScoreDrawer (props) {

  const isAtsSelected = JSON.parse(getSessionData('atsSelectedCandidate'))
  const [currentTab, setCurrentTab] = useState()
  // console.log({isAtsSelected})
  if (!isAtsSelected) {
    return (<React.Fragment></React.Fragment>)
  }

  const { details: { firstName, lastName, id } } = props
  const { score, maxScore, detailScore } = isAtsSelected

  if (!detailScore) return <React.Fragment></React.Fragment>

  const result = {}
  detailScore.forEach(item => {
    const key = item.entity
    if (!result[key]) {
      result[key] = []
    }
    result[key].push(item)
  })

  const scoreClass = cx({
    'low': score <= 35,
    'medium': score >= 35 && score < 72,
    'high': score >= 72 && score <= 100
  })

  const TOOLTIP_CONTENT = {
    'CurrentJobProfile': 'The last designation held by the candidate.',
    'HighestDegree': 'The last educational qualification of the candidate.',
    'Degree': 'The last educational qualification of the candidate.',
    'Category': 'Weightage applied to match JD Domains/Industry with Resume Categories.',
    'SkillWithoutExp': 'Weight applied to match skills mentioned in Job description with Resume skills.',
    'RequiredSkillSet': 'Match against the required skills mentioned in the job description.',
    'SkillAlias': 'Similar skills to ones required in the job description.',
    'SkillsHaveExp': 'This weight scales with experience acquired on the particular skills, matching JD & Resume.'
  }

  const handleCollapse = (keyName) => {
    console.log(window.collapseName, keyName)
    if (window.collapseName === keyName) {
      setCurrentTab('')
      window.collapseName = ''
      return
    }
    window.collapseName = keyName
    setCurrentTab(keyName)
  }

  const AtsScoreHeader = () => {
    return (<div className='atsScoreHeader'>
      <UserAvatar details={props.details} className='image' />
      <div className='content'>
        <h4>{firstName} {lastName}</h4>
        <p>{detailScore[0].value || '' }</p>
      </div>
      <div className={`score ${scoreClass}`}>
        <span className='label'>score</span>
        <span className='value'>{scoreToFixed(score)}/{maxScore}</span>
      </div>
    </div>)
  }

  const TableHeader = (entity) => (<Table.Head>
    <Table.Row>
      <Table.Th className='w30' data-display={entity == 1}>
        JD Attribute
        <Tooltip className='tooltip' trigger='mouseenter' content={`This represents specific section in the candidate's resume that Olivia AI is analyzing.`}>
          <img className='icon-info' src={ICONS.info} />
        </Tooltip>
      </Table.Th>
      <Table.Th className='w65'>
        Value
        <Tooltip className='tooltip' trigger='mouseenter' content={`This corresponds to the candidate's resume in relation to the provided attribute.`}>
          <img className='icon-info' src={ICONS.info} />
        </Tooltip>
      </Table.Th>
      <Table.Th>
        Score
        <Tooltip className='tooltip' trigger='mouseenter' content={'Reflects candidate alignment with job attributes outlined in the job description'}>
          <img className='icon-info' src={ICONS.info} />
        </Tooltip>
      </Table.Th>
      <Table.Th className='lastCell'>
        Max Score
        <Tooltip className='tooltip' trigger='mouseenter' content={'Indicates the highest achievable score for each attribute.'}>
          <img className='icon-info' src={ICONS.info} />
        </Tooltip>
      </Table.Th>
    </Table.Row>
  </Table.Head>)

  return (
    <div className='atsScoreInfo'>
      <AtsScoreHeader />

      <Table>
        <TableHeader entity={1} />
        <Table.Body>
          { detailScore && detailScore.map(row => {
            if (scoreToFixed(row.score, 0) == 0 || row.entity === 'SkillWithoutExp' || row.entity === 'SkillHaveExp' || row.entity === 'SkillAlias') return
            return (
              <Table.Row>
                <Table.Td>{camelCaseToWords(row.entity)}
                  <Tooltip className='tooltip' trigger='mouseenter' content={`${TOOLTIP_CONTENT[row.entity] ? TOOLTIP_CONTENT[row.entity] : camelCaseToWords(row.entity)}`}>
                    <img className='icon-info' src={ICONS.info} />
                  </Tooltip>
                </Table.Td>
                <Table.Td>{(row.value).replaceAll('-Infinity TO ', '')}</Table.Td>
                <Table.Td className='bold'>{scoreToFixed(row.score)}</Table.Td>
                <Table.Td className='bold'>{row.maxScore}</Table.Td>
              </Table.Row>
            )
          }
          )}
        </Table.Body>
      </Table>

      <Table>
        {Object.entries(result).map(([key, value]) => {
          if (value.length == 1) return
          return <React.Fragment>
            {/* {JSON.stringify(value.length)} */}

            <Table.Body className={(key === currentTab)}>
              <Table.Row>
                <Table.Td colSpan={4}>
                  <div onClick={() => handleCollapse(key)} role='button'>
                    <strong>{(key === currentTab) ? '-' : '+'} &nbsp;</strong>
                    <label for={key} onClick={() => handleCollapse(key)} role='button'>{camelCaseToWords(key)}</label>
                    <input type='checkbox' id={key} className='innerCheck' />
                    <Tooltip className='tooltip text-left' style={{ textAlign: 'left' }}
                      trigger='mouseenter' content={`${TOOLTIP_CONTENT[key] ? TOOLTIP_CONTENT[key] : camelCaseToWords(key)}`}>
                      <img className='icon-info' src={ICONS.info} />
                    </Tooltip>
                  </div>
                </Table.Td>
              </Table.Row>
            </Table.Body>
            <Table.Body className='innerList'>
              <TableHeader />
              {value.map(val => (<Table.Row>
                <Table.Td>
                  {camelCaseToWords(val.entity)}
                  
                </Table.Td>
                <Table.Td>
                  {(val.value).replaceAll('TO Infinity', '').replace('[', '').replace(']', '')}
                </Table.Td>
                <Table.Td>
                  {val.score}
                </Table.Td>
                <Table.Td>
                  {val.maxScore}
                </Table.Td>
              </Table.Row>))}
            </Table.Body>
          </React.Fragment>
        }) }
      </Table>

      <div className='form__wrapper--drawer-actions'>
        <div className='form__wrapper--button-group apply-form-buttons'>
          <a className='btn btn-primary' href={`/employer/candidate/${id}`} target='_blank'>View full profile</a>

          <div className='bottom-text'>Got Questions? Email <a href='mailto:team@oliv.com'><strong>team@oliv.com</strong>.</a> <br /> 
Alternatively you can schedule recruitment consultancy with our experts <a href='https://calendly.com/adeel-oliv/interviews' target='_blank'><strong>here</strong></a>.</div>
        </div>
      </div>
    </div>
  )
}

export default ScoreDrawer



